<template>
  <div id="app" v-if="logined">
    <base-header />
    <the-tab-bar />
    <keep-alive>
      <router-view />
    </keep-alive>
    <player />
  </div>
</template>
<script>
import BaseHeader from 'components/content/BaseHeader.vue';
import TheTabBar from 'components/content/TheTabBar.vue';
import Player from 'components/content/Player/Player.vue';
import login from 'api/login';

export default {
  components: {
    BaseHeader,
    TheTabBar,
    Player,
  },
  created() {
    login().then((res) => {
      console.log(res);
      this.logined = true;
      this.$store.commit({
        type: 'changeUserCookie',
        cookie: res.cookie,
      });
    });
  },
  data() {
    return {
      logined: false,
    };
  },
};
</script>
