<template>
  <div>
    <ul class="songs-list" v-if="songsInfoLoaded" @click="getSongListDetail($event)">
      <li
        class="song-item"
        v-for="(item, index) in songsInfo"
        :key="index"
        :data-index="index"
      >
        <div class="song-icon">
          <img v-lazy="item.imgSrc" alt="">
        </div>
        <div class="song-detail">
          <h2>{{item.name}}</h2>
          <p>{{item.details}}</p>
        </div>
      </li>
    </ul>
    <loading class="loading" v-if="!songsInfoLoaded"></loading>
  </div>
</template>
<script>
import getSongsList from 'api/recommend/songsList';
import Loading from 'components/common/loading/Loading.vue';

export default {
  created() {
    getSongsList(this.userCookie).then((res) => {
      this.songsInfo = res.recommend.map((item) => ({
        name: item.name,
        details: item.copywriter,
        imgSrc: item.picUrl,
        id: item.id,
      }));
      this.songsInfoLoaded = true;
      this.$emit('songs-list-loaded');
    }).catch((err) => {
      console.log(err);
    });
  },
  data() {
    return {
      songsInfo: [],
      songsInfoLoaded: false,
    };
  },
  computed: {
    userCookie() {
      return this.$store.state.userCookie;
    },
  },
  components: {
    Loading,
  },
  methods: {
    getSongListDetail(event) {
      const target = event.target.closest('.song-item');
      if (!target) {
        return;
      }
      const { index } = target.dataset;
      const { id, imgSrc, name } = this.songsInfo[index];
      this.$emit('enter-songlist', {
        id,
        imgSrc,
        name,
      });
    },
  },
  emits: ['songs-list-loaded', 'enter-songlist'],
};
</script>
