      > .song-icon {
        padding-right: 20px;
        flex: 0 0 60px;
        > img {
          width: 60px;
          height: 60px;
        }
      }
      > .song-detail {
        flex: 1;
        font-size: $font-size-medium;
        > h2 {
          color: $color-text;
          margin-bottom: 10px;
        }
        > p {
          color: $color-text-d;
        }
      }
    }
  }
  .loading {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
</style>
