import storage from './storage';

const SEARCH_KEY = '_searchHistroy_';
const SEARCH_MAX_LENGTH = 15;
const LISTEN_KEY = '_listenHistory_';
const LISTEN_MAX_LENGTH = 100;
const USER_LOVE_SONGS = '_userLoveSongs_';
const USER_LOVE_SONGS_MAX_LENGTH = 100;

function _insertArr(arr, value, length, compare) {
  const index = arr.findIndex(compare);
  if (index === 0) {
    return;
  }
  if (index > 0) {
    arr.splice(index, 1);
  }
  arr.unshift(value);
  if (arr.length > length) {
    arr.pop();
  }
}

function saveSearch(query) {
  const res = storage.get(SEARCH_KEY) || [];
  _insertArr(res, query, SEARCH_MAX_LENGTH, (item) => item === query);
  storage.set(SEARCH_KEY, res);
  return res;
}
function getSearchHistroy() {
  return storage.get(SEARCH_KEY);
}
function deleteHistory(query, all) {
  if (all) {
    storage.set(SEARCH_KEY, []);
    return [];
  }
  const historyArr = storage.get(SEARCH_KEY);
  const index = historyArr.findIndex((item) => item === query);
  historyArr.splice(index, 1);
  storage.set(SEARCH_KEY, historyArr);
  return historyArr;
}

function saveListenHistory(song) {
  const res = storage.get(LISTEN_KEY) || [];
  _insertArr(res, song, LISTEN_MAX_LENGTH, (item) => item.id === song.id);
  storage.set(LISTEN_KEY, res);
  return res;
}
function deleteListenHistory(song, all) {
  if (all) {
    storage.set(LISTEN_KEY, []);
    return [];
  }
  const historyArr = storage.get(LISTEN_KEY);
  const index = historyArr.findIndex((item) => item === song);
  historyArr.splice(index, 1);
  storage.set(SEARCH_KEY, historyArr);
  return historyArr;
}
function getListenHistory() {
  return storage.get(LISTEN_KEY);
}

function saveUserLoveSongs(song) {
  const res = storage.get(USER_LOVE_SONGS) || [];
  _insertArr(res, song, USER_LOVE_SONGS_MAX_LENGTH, (item) => item.id === song.id);
  storage.set(USER_LOVE_SONGS, res);
  return res;
}
function deleteUserLoveSongs(song, all) {
  if (all) {
    storage.set(USER_LOVE_SONGS, []);
    return [];
  }
  const historyArr = storage.get(USER_LOVE_SONGS);
  const index = historyArr.findIndex((item) => item.id === song.id);
  historyArr.splice(index, 1);
  storage.set(USER_LOVE_SONGS, historyArr);
  return historyArr;
}
function getUserLoveSongs() {
  return storage.get(USER_LOVE_SONGS);
}
export {
  saveSearch,
  getSearchHistroy,
  deleteHistory,
  saveListenHistory,
  deleteListenHistory,
  getListenHistory,
  getUserLoveSongs,
  deleteUserLoveSongs,
  saveUserLoveSongs,
};
