<template>
  <div class="player" v-show="playList.length">
    <transition name="normal">
      <div class="normal-player" v-show="fullScreen">
        <div class="background" :style=bgSrc></div>
        <base-nav-bar class="nav-bar">
          <template #nav-left>
            <div class="back" @click="back">
              <i class="icon-back"></i>
            </div>
          </template>
          <template #nav-center>
            <div class="title">
              <h1 class="songTitle">{{getCurrentSong && getCurrentSong.name || '正在加载'}}</h1>
              <p class="singerName">{{singerName || '正在加载'}}</p>
            </div>
          </template>
        </base-nav-bar>
        <div class="CD">
          <div
            class="cd-content"
            ref="cdContent"
            @touchstart.prevent="cdTouchStart($event)"
            @touchmove.prevent="cdTouchMove($event)"
            @touchend.prevent="cdTouchEnd($event)"
          >
            <div class="cd-l">
              <div class="cd-image">
                <div class="cd-main">
                  <div
                    class="cd-bg"
                    :style="bgSrc"
                    :class="{'animation-pause': !playing}"
                  ></div>
                </div>
              </div>
            </div>
            <div class="cd-r">
              <player-lyric
                ref="lyric"
                class="lyric"
                v-if="lyric"
                :lyric="lyric.lines"
                :currentLineIndex="currentLineIndex"
              ></player-lyric>
            </div>
          </div>
        </div>
        <div class="footer-bar" @click="normalOperatorHandle($event)">
          <div class="dot-wrapper">
            <div class="dot" :class="{active: currentCd === 1}"></div>
            <div class="dot" :class="{active: currentCd === 2}"></div>
          </div>
          <div class="progress-wrapper">
            <p class="current-time">{{timeNow}}</p>
            <progress-bar
              class="progress"
              :percent="percent"
              :progress-width="progressWidth"
              ref="progress"
              @percent-change="handlePercentChange"
            />
            <p class="total-time">{{totalTimeString}}</p>
          </div>
          <div class="operators">
            <div class="playFuncButton" data-index="0">
              <i :class="iconMode"></i>
            </div>
            <div class="playFuncButton" data-index="1">
              <i class="icon-prev"></i>
            </div>
            <div class="playFuncButton operators-center" data-index="2">
              <i class="center" :class="playButtonClass"></i>
            </div>
            <div class="playFuncButton" data-index="3">
              <i class="icon-next"></i>
            </div>
            <div class="playFuncButton" data-index="4">
              <i :class="heartClass"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="mini">
      <div
        class="mini-player"
        v-show="!fullScreen"
        ref="miniPlayer"
        @click="miniClickHandle"
      >
        <div class="mini-cd" :style="bgSrc" :class="{'animation-pause': !playing}"></div>
        <div class="mini-title">
          <h2 class="name">{{getCurrentSong && getCurrentSong.name || '正在加载'}}</h2>
          <p class="singerName">{{singerName || '正在加载'}}</p>
        </div>
        <div class="playControlButtons" @click.stop="miniPlayerControl($event)">
          <div class="button1" data-index="0">
            <progress-circle class="progress-circle" :percent="percent">
              <i :class="miniPlayButtonClass"></i>
            </progress-circle>
          </div>
          <div class="button2" data-inedx="1">
            <i class="icon-playlist"></i>
          </div>
        </div>
      </div>
    </transition>
    <base-play-list :show="showPlayList" @close="showPlayList=false" @func-handle="funcHandle"/>
    <audio
      :src="getCurrentSong && getCurrentSong.url"
      id="music"
      @canplay="playMusic"
      @timeupdate="updateTime"
      @ended="musicEndHandle"
      ref="audio"
    ></audio>
  </div>
</template>
<script>
import { createNamespacedHelpers } from 'vuex';
import ProgressBar from 'components/content/ProgressBar.vue';
import ProgressCircle from 'components/content/ProgressCircle.vue';
import BaseNavBar from 'components/common/BaseNavBar/BaseNavBar.vue';
import PlayerLyric from 'components/content/Lyric.vue';
import BasePlayList from 'components/content/BasePlayList.vue';
import { playMode } from 'src/config';
import Lyric from 'assets/js/tool/lyricParser';
import {
  saveListenHistory,
  saveUserLoveSongs,
  deleteUserLoveSongs,
} from 'assets/js/tool/cache';

const {
  mapState,
  mapGetters,
  mapMutations,
  mapActions,
} = createNamespacedHelpers('songModule');
export default {
  components: {
    BaseNavBar,
    ProgressBar,
    ProgressCircle,
    PlayerLyric,
    BasePlayList,
  },
  data() {
    return {
      currentTime: 0,
      totalTime: 0,
      percent: 0,
      totalTimeString: 0,
      progressWidth: 0,
      modeNumber: 0,
      lyric: null,
      touch: {},
      currentCd: 1,
      currentLineIndex: 0,
      showPlayList: false,
    };
  },
  computed: {
    singerName() {
      if (this.getCurrentSong) {
        return this.getCurrentSong.singerName.join(',');
      }
      return false;
    },
    bgSrc() {
      const styleObject = {
        backgroundImage: `url(${this.getCurrentSong && this.getCurrentSong.icon})`,
      };
      return styleObject;
    },
    playButtonClass() {
      return !this.playing ? 'icon-play' : 'icon-pause';
    },
    miniPlayButtonClass() {
      return !this.playing ? 'icon-play-mini' : 'icon-pause-mini';
    },
    timeNow() {
      if (~this.currentIndex) {
        const minute = this._fillIn(Math.floor(this.currentTime / 60));
        const second = this._fillIn(Math.floor(this.currentTime - minute * 60));
        return `${minute}:${second}`;
      }
      return 0;
    },
    iconMode() {
      let icon = '';
      switch (this.mode) {
        case playMode.ORDER:
          icon = 'icon-sequence';
          break;
        case playMode.RANDOM:
          icon = 'icon-random';
          break;
        default:
          icon = 'icon-loop';
      }
      return icon;
    },
    heartClass() {
      return this.getCurrentSong && this.getCurrentSong.loved ? 'icon-favorite' : 'icon-not-favorite';
    },
    ...mapState([
      'playList',
      'playing',
      'mode',
      'fullScreen',
      'currentIndex',
      'songList',
    ]),
    ...mapGetters([
      'getCurrentSong',
    ]),
  },
  methods: {
    back() {
      this.setFullScreen(false);
    },
    miniPlayerControl(event) {
      const target = event.target.closest('[class^="button"');
      if (!target) {
        return;
      }
      const { index } = target.dataset;
      if (index === '0') {
        this.setPlaying(!this.playing);
      } else {
        this.showPlayList = true;
      }
    },
    normalOperatorHandle(event) {
      const target = event.target.closest('.playFuncButton');
      if (!target) {
        return;
      }
      const { index } = target.dataset;
      let result;
      switch (index) {
        case '0':
          this._changePlayMode();
          break;
        case '1':
          this.nextOrPre(-1);
          break;
        case '2':
          this.setPlaying(!this.playing);
          break;
        case '3':
          this.nextOrPre(1);
          break;
        default:
          this.getCurrentSong.loved = !this.getCurrentSong.loved;
          if (this.getCurrentSong.loved) {
            result = saveUserLoveSongs(this.getCurrentSong);
          } else {
            result = deleteUserLoveSongs(this.getCurrentSong);
          }
          this.$store.commit('setUserLoveSongs', result);
      }
    },
    nextOrPre(flag) {
      let index = this.currentIndex + flag;
      if (index < 0) {
        index = this.playList.length - 1;
      }
      if (index >= this.playList.length) {
        index = 0;
      }
      this.setCurrentIndex(index);
      if (!this.playing) {
        this.setPlaying(true);
      }
    },
    playMusic() {
      if (!this.playing) {
        return;
      }
      this.$refs.audio.play();
      this.lyric.play();
    },
    updateTime() {
      this.currentTime = this.$refs.audio.currentTime;
      this.percent = this.currentTime / this.totalTime;
    },
    handlePercentChange(newValue) {
      this.percent = newValue;
      this.$refs.audio.currentTime = this.percent * this.totalTime;
      this.lyric.leap(this.$refs.audio.currentTime * 1000);
    },
    miniClickHandle() {
      this.setFullScreen(true);
    },
    lyricHandle(el) {
      this.currentLineIndex = el.index;
    },
    cdTouchStart(event) {
      this.touch.startTouchX = event.touches[0].pageX;
      this.touch.startTouchY = event.touches[0].pageY;
    },
    cdTouchMove(event) {
      const x = event.touches[0].pageX;
      const y = event.touches[0].pageY;
      this.touch.offset = x - this.touch.startTouchX;
      this.touch.offsetY = y - this.touch.startTouchY;
      if (Math.abs(this.touch.offset) < Math.abs(this.touch.offsetY)) {
        this.touch.percent = 0;
        return;
      }
      const left = this.currentCd === 1 ? 0 : -window.innerWidth;
      this.touch.percent = Math.abs(this.touch.offset) / window.innerWidth;
      this.touch.offset = Math.min(0, Math.max(-window.innerWidth, left + this.touch.offset));
      this.$refs.cdContent.style.transform = `translateX(${this.touch.offset}px)`;
    },
    cdTouchEnd() {
      if (this.touch.percent < 0.25) {
        this.touch.offset = this.currentCd === 1 ? 0 : -window.innerWidth;
      } else if (this.touch.offset === 0) {
        this.touch.offset = 0;
        this.currentCd = 1;
      } else if (this.touch.offset === -window.innerWidth) {
        this.touch.offset = -window.innerWidth;
        this.currentCd = 2;
      } else {
        this.currentCd = this.currentCd === 1 ? 2 : 1;
        this.touch.offset = this.currentCd === 1 ? 0 : -window.innerWidth;
      }
      this.$refs.cdContent.style.transition = '500ms';
      this.$refs.cdContent.style.transform = `translateX(${this.touch.offset}px)`;
      setTimeout(() => {
        this.$refs.cdContent.style.transition = '0ms';
      }, 500);
    },
    musicEndHandle() {
      if (this.mode === playMode.SINGER_CYCLE) {
        this.handlePercentChange(0);
        return;
      }
      const newCurrentIndex = this.currentIndex
        === this.playList.length - 1 ? 0 : this.currentIndex + 1;
      this.setCurrentIndex(newCurrentIndex);
    },
    funcHandle(payload) {
      const { funcIndex, songIndex } = payload;
      switch (funcIndex) {
        case 0:
          this._changePlayMode();
          break;
        case 1:
          this.clearSongList();
          this.showPlayList = false;
          console.log(this.showPlayList);
          break;
        case 2:
          this.setCurrentIndex(songIndex);
          break;
        case 3:
          this.removeSong(this.songList[songIndex]);
          break;
        default:
          break;
      }
    },
    test() {
      this.showPlayList = false;
    },
    _changePlayMode() {
      let newMode = 0;
      this.modeNumber = (this.modeNumber + 1) % 3;
      if (this.modeNumber === 0) {
        newMode = playMode.ORDER;
      } else if (this.modeNumber === 1) {
        newMode = playMode.RANDOM;
      } else {
        newMode = playMode.SINGER_CYCLE;
      }
      this.changePlayMode(newMode);
    },
    _play() {
      const minute = this._fillIn(this.getCurrentSong.minutes);
      const second = this._fillIn(this.getCurrentSong.seconds);
      this.totalTimeString = `${minute}:${second}`;
      this.totalTime = this.getCurrentSong.minutes * 60 + this.getCurrentSong.seconds;
      this.lyric = new Lyric(this.getCurrentSong.lyric, this.lyricHandle);
      const historyArr = saveListenHistory(this.getCurrentSong);
      this.$store.commit('setListenHistory', historyArr);
    },
    _fillIn(val) {
      let nVal = val;
      if (val < 10) {
        nVal = `0${val}`;
      }
      if (val >= 100) {
        nVal = val.toString().slice(0, 2);
      }
      return nVal;
    },
    ...mapMutations([
      'setFullScreen',
      'setMiniPlayerHeight',
      'setPlaying',
      'setCurrentIndex',
      'setPlayMode',
    ]),
    ...mapActions([
      'changePlayMode',
      'clearSongList',
      'removeSong',
    ]),
  },
  watch: {
    getCurrentSong(nValue, oValue) {
      if (nValue === oValue) {
        return;
      }
      if (!this.getCurrentSong) {
        this.$refs.audio.pause();
        this.lyric.reset();
        this.lyric = null;
        return;
      }
      if (this.lyric) {
        this.lyric.reset();
      }
      if (!nValue.enable) {
        nValue.getPlayInfo().then(() => {
          this._play();
        });
      } else {
        this._play();
      }
    },
    playing(nVal) {
      if (this.$refs.audio.readyState !== 4) {
        return;
      }
      if (nVal) {
        this.$refs.audio.play();
        this.lyric.play();
      } else {
        this.$refs.audio.pause();
        this.lyric.stop();
      }
    },
  },
  mounted() {
    const unWatch = this.$watch('fullScreen', function getMiniPlayerHeight(nVal) {
      if (!nVal) {
        this.setMiniPlayerHeight(this.$refs.miniPlayer.offsetHeight);
        unWatch();
      } else {
        this.progressWidth = this.$refs.progress.$el.clientWidth;
        setTimeout(() => {
          this.progressWidth = this.$refs.progress.$el.clientWidth;
        }, 500);
      }
    });
  },
};
</script>
<style lang="scss" scoped>
