export default {
  state() {
    return {
      songList: [],
      songIdList: [],
      name: '',
      icon: '',
    };
  },
  mutations: {
    setSongList(state, value) {
      state.songList = value;
    },
    setSongIdList(state, value) {
      state.songIdList = value;
    },
    setName(state, value) {
      state.name = value;
    },
    setIcon(state, value) {
      state.icon = value;
    },
  },
  namespaced: true,
};
