import { playMode } from 'src/config';
import shuffle from 'assets/js/tool/shuffle';

// 当播放列表为空，新增歌曲时，初始化播放功能
function initPlay({ commit }, payLoad) {
  commit('setSongList', payLoad.songList);
  commit('setPlayList', payLoad.songList);
  commit('setPlayMode', playMode.ORDER);
  commit('setCurrentIndex', 0);
  commit('setPlaying', true);
  commit('setFullScreen', true);
}
function changeCurrentSong({
  state,
  commit,
  dispatch,
  getters,
}, payLoad) {
  if (~state.currentIndex && payLoad.songList[0] !== getters.getCurrentSong) {
    dispatch('insertSong', payLoad.songList);
    // 切歌
    commit('setCurrentIndex', state.currentIndex + 1);
  } else {
    dispatch('initPlay', payLoad);
  }
  commit('setFullScreen', true);
}
// 把新歌添加到当前歌曲的下一首
function insertSong({ state, commit }, List) {
  const song = List[0];
  const playList = state.playList.slice();
  const songList = state.songList.slice();
  const { length } = playList;
  let { currentIndex } = state;
  // 查找原播放列表是否已经含有该歌曲
  const fIndex = playList.findIndex((item) => item === song || item.id === song.id);
  const fSIndex = songList.findIndex((item) => item === song || item.id === song.id);
  if (~fIndex) {
    if (fIndex < currentIndex) {
      currentIndex -= 1;
    }
    playList.splice(fIndex, 1);
    songList.splice(fSIndex, 1);
  }
  songList.splice(currentIndex + 1, 0, song);
  //  插入该歌曲
  if (currentIndex === length - 1) {
    playList.push(song);
  } else {
    playList.splice(currentIndex + 1, 0, song);
  }
  commit('setCurrentIndex', currentIndex);
  commit('setSongList', songList);
  commit('setPlayList', playList);
}

function changeSongList({ commit, dispatch }, songList) {
  commit('setSongList', songList);
  dispatch('changePlayMode');
}

function changePlayMode({ state, commit, getters }, nplayMode) {
  const mode = nplayMode !== undefined ? nplayMode : state.mode;
  const { songList } = state;
  let nCurrentIndex;
  let playList = null;
  if (mode === playMode.RANDOM) {
    playList = shuffle(songList.slice());
  } else {
    playList = songList.slice();
  }
  if (state.currentIndex === -1) {
    nCurrentIndex = 0;
  } else {
    nCurrentIndex = playList.indexOf(getters.getCurrentSong);
  }
  commit('setPlayList', playList);
  commit('setCurrentIndex', nCurrentIndex);
  commit('setPlayMode', mode);
  commit('setPlaying', true);
}

function removeSong({ state, commit, getters }, song) {
  const playList = state.playList.slice();
  const songList = state.songList.slice();
  if (getters.getCurrentSong === song) {
    playList.splice(state.currentIndex, 1);
  } else {
    const fPIndex = playList.indexOf(song);
    if (fPIndex < state.currentIndex) {
      commit('setCurrentIndex', state.currentIndex - 1);
    }
    playList.splice(fPIndex, 1);
  }
  const fIndex = songList.indexOf(song);
  songList.splice(fIndex, 1);
  commit('setSongList', songList);
  commit('setPlayList', playList);
}

function clearSongList({ commit }) {
  commit('setCurrentIndex', -1);
  commit('setSongList', []);
  commit('setPlayList', []);
}
export {
  initPlay,
  changeCurrentSong,
  insertSong,
  changeSongList,
  changePlayMode,
  removeSong,
  clearSongList,
};
