<template>
  <div class="slider" ref="slider">
    <scroll
      ref="scroll"
      class="slider-wrapper"
      :scroll-direction="1"
      @mounted="_setSliderContentWidth"
      :slide="true"
      :slide-options="{
        loop,
        autoPlay,
        interval,
        speed,
        threshold,
      }"
      @slide-will-change="changeCurrentPageIndex"
    >
      <div class="slider-item" v-for="(item, index) in sliderData" :key="index">
        <a :href="item.href">
          <img :src="item.imgSrc" alt @load="$emit('img-loaded')"/>
        </a>
      </div>
    </scroll>
    <div class="dots">
      <span
        class="dots-item"
        v-for="(item, index) in dots"
        :key="index"
        :class="{active: index===currentPageIndex}"
      ></span>
    </div>
  </div>
</template>
<script>
import Scroll from 'components/common/scroll/Scroll.vue';

export default {
  components: {
    Scroll,
  },
  computed: {
    dots() {
      return new Array(this.sliderData.length);
    },
  },
  data() {
    return {
      currentPageIndex: 0,
    };
  },
  props: {
    sliderData: {
      type: Array,
      required: true,
    },
    loop: {
      type: Boolean,
      default: true,
    },
    autoPlay: {
      type: Boolean,
      default: true,
    },
    interval: {
      type: Number,
      default: 3000,
    },
    speed: {
      type: Number,
      default: 400,
    },
    threshold: {
      type: Number,
      default: 0.2,
    },
  },
  methods: {
    _setSliderWidth() {
      const boxWidth = this.$refs.slider.offsetWidth;
      const sliderItems = this.$refs.slider.querySelectorAll('.slider-item');
      Array.from(sliderItems).forEach((el) => {
        const temp = el;
        temp.style.width = `${boxWidth}px`;
      });
    },
    _setSliderContentWidth() {
      if (!this.loop) {
        return;
      }
      const boxWidth = this.$refs.slider.offsetWidth;
      let sliderContentWidth = 0;
      const sliderItemsSize = this.$refs.slider.querySelectorAll('.slider-item').length;
      sliderContentWidth = boxWidth * (sliderItemsSize + 2);
      this.$refs.scroll.$refs.content.style.width = `${sliderContentWidth}px`;
      this.$refs.scroll.refresh();
    },
    changeCurrentPageIndex(page) {
      this.currentPageIndex = page.pageX;
    },
  },
  mounted() {
    setTimeout(this._setSliderWidth, 20);
  },
  deactivated() {
    this.$refs.scroll.pausePlay();
  },
  activated() {
    this.$refs.scroll.startPlay();
  },
};
</script>
<style lang="scss" scoped>
@import "~assets/style/variable.scss";
.slider {
  position: relative;
  .slider-wrapper {
    .slider-item {
      display: inline-block;
      > a {
        display: block;
        > img {
          display: block;
          width: 100%;
        }
      }
    }
  }
  .dots {
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 12px;
    text-align: center;
    font-size: 0px;
    > .dots-item {
      display: inline-block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: $color-text-l;
      margin: 0 4px;
      &.active {
        width: 20px;
        border-radius: 5px;
        background: $color-text-ll;
      }
    }
  }
}
</style>
