const timeExp = /\[(\d{2,}):(\d{2,}).(\d{2,3})\]/;

export default class Lyric {
  constructor(lyricStr, handle) {
    this.lyricStr = lyricStr;
    this.lines = [];
    this.nowLyricIndex = 0;
    this.handle = handle;
    this.timeId = null;
    this.offset = 0;
    this._init();
  }

  _init() {
    const lyricArr = this.lyricStr.split('\n');
    lyricArr.forEach((item) => {
      const times = timeExp.exec(item);
      if (times) {
        const lyric = item.replace(times[0], '').trim();
        this.lines.push({
          time: (parseInt(times[1], 10) * 60 + parseInt(times[2], 10))
          * 1000
          + parseInt(times[3], 10),
          lyric,
        });
      }
    });
  }

  _handle() {
    this.handle({
      index: this.nowLyricIndex,
      content: this.lines[this.nowLyricIndex].lyric,
    });
  }

  _findNowLyric(time) {
    let nowLyricIndex = -1;
    this.lines.some((item, index) => {
      if (item.time <= time && this.lines[index + 1].time > time) {
        nowLyricIndex = index;
        return true;
      }
      return false;
    });
    return nowLyricIndex;
  }

  _playNext() {
    this._handle();
    if (this.nowLyricIndex >= this.lines.length - 1) {
      return;
    }
    const delay = this.lines[this.nowLyricIndex + 1].time
    - this.lines[this.nowLyricIndex].time
    - this.offset;
    this.nowLyricStartTime = +new Date();
    this.timeId = setTimeout(() => {
      this.nowLyricIndex += 1;
      this.offset = 0;
      this._playNext();
    }, delay);
  }

  play() {
    if (this.timeId) {
      return;
    }
    this.stoped = false;
    this._playNext();
  }

  stop() {
    if (this.timeId) {
      clearTimeout(this.timeId);
      this.timeId = null;
    }
    this.stoped = true;
    const stopTime = +new Date();
    this.offset += stopTime - this.nowLyricStartTime;
  }

  leap(startTime) {
    if (this.timeId) {
      clearTimeout(this.timeId);
      this.timeId = null;
    }
    this.nowLyricIndex = this._findNowLyric(startTime);
    if (this.nowLyricIndex >= this.lines.length - 1) {
      this.offset = 0;
    } else {
      this.offset = startTime - this.lines[this.nowLyricIndex].time;
    }
    if (!this.stoped) {
      this._playNext();
    }
  }

  reset() {
    clearTimeout(this.timeId);
    this.timeId = 0;
    this.offset = 0;
    this.nowLyricIndex = 0;
  }
}
