import Vue from 'vue';
import Vuex from 'vuex';
import {
  getSearchHistroy,
  getListenHistory,
  getUserLoveSongs,
} from 'assets/js/tool/cache';
import songModule from './modules/songModule';
import rankDetailModule from './modules/rankDetailModule';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userCookie: '',
    searchHistory: getSearchHistroy(),
    loveSongs: getUserLoveSongs(),
    listenHistory: getListenHistory(),
  },
  mutations: {
    changeUserCookie(state, payload) {
      state.userCookie = payload.cookie;
    },
    setSearchHistory(state, payload) {
      state.searchHistory = payload;
    },
    setUserLoveSongs(state, payload) {
      state.loveSongs = payload;
    },
    setListenHistory(state, payload) {
      state.listenHistory = payload;
    },
  },
  actions: {
  },
  modules: {
    songModule,
    rankDetailModule,
  },
});
