import random from './random';

function shuffle(arr) {
  const { length } = arr;
  const nArr = arr.slice();
  nArr.forEach((item, index) => {
    const randomIndex = random(index, length)();
    nArr[index] = nArr[randomIndex];
    nArr[randomIndex] = item;
  });
  return nArr;
}
export default shuffle;
