<template>
  <transition name="transition">
    <base-dialog
      @bg-cover-click="$emit('close')"
      v-show="show"
    >
      <div class="play-list-content" @click="handleClick($event)">
        <div class="header">
          <h1 class="title">当前播放<span>({{playList.length}})</span></h1>
          <div class="header-operators">
            <div class="play-mode operator">
              <span :class="modeClass"></span>
              <span class="modeText">{{modeText}}</span>
            </div>
            <div class="delete-btn operator">
              <span class="icon-clear"></span>
            </div>
          </div>
        </div>
        <scroll class="content" :dataArr="songList" ref="scroll">
          <ul class="play-list">
            <transition-group name="song">
              <li
                class="item"
                v-for="(item, index) in songList"
                :key="item.id"
                :data-index="index"
                :class="{active: isCurrentSong(item)}"
              >
                <p class="singer-info">
                  <svg
                    class="icon yinfu"
                    aria-hidden="true"
                    v-show="isCurrentSong(item)"
                  >
                    <use xlink:href="#icon-yinfu"></use>
                  </svg>
                  <span class="song-name">{{item.name}}</span>
                  <span class="singer-name">-{{getSingerName(item)}}</span>
                </p>
                <span class="icon-delete"></span>
              </li>
            </transition-group>
          </ul>
        </scroll>
      </div>
    </base-dialog>
  </transition>
</template>

<script>
import BaseDialog from 'components/common/dialog/BaseDialog.vue';
import Scroll from 'components/common/scroll/Scroll.vue';
import { createNamespacedHelpers } from 'vuex';
import { playMode } from 'src/config';

const { mapState } = createNamespacedHelpers('songModule');
export default {
  components: {
    BaseDialog,
    Scroll,
  },
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    show(nValue) {
      if (nValue) {
        this.$refs.scroll.refresh();
      }
    },
  },
  computed: {
    modeClass() {
      let icon = '';
      switch (this.mode) {
        case playMode.ORDER:
          icon = 'icon-sequence';
          break;
        case playMode.RANDOM:
          icon = 'icon-random';
          break;
        default:
          icon = 'icon-loop';
      }
      return icon;
    },
    modeText() {
      let text = '';
      switch (this.mode) {
        case playMode.ORDER:
          text = '顺序播放';
          break;
        case playMode.RANDOM:
          text = '随机播放';
          break;
        default:
          text = '单曲循环';
      }
      return text;
    },
    ...mapState([
      'mode',
      'playList',
      'songList',
      'currentIndex',
    ]),
  },
  methods: {
    getSingerName(item) {
      const { singerName } = item;
      return singerName.join('/');
    },
    handleClick(event) {
      const operatorTarget = event.target.closest('.operator');
      const itemTarget = event.target.closest('.item');
      if (!operatorTarget && !itemTarget) {
        return;
      }
      let funcIndex = 0;
      if (operatorTarget && !itemTarget) {
        funcIndex = operatorTarget.classList[0] === 'play-mode' ? 0 : 1;
      } else {
        const deleteNode = event.target.closest('.icon-delete');
        funcIndex = deleteNode ? 3 : 2;
        if (funcIndex === 3 && this.playList.length === 1) {
          funcIndex = 1;
        }
      }
      this.$emit('func-handle', {
        funcIndex,
        songIndex: funcIndex > 1 ? itemTarget.dataset.index : null,
      });
    },
    isCurrentSong(item) {
      return this.playList[this.currentIndex] === item;
    },
  },
  created() {},
};
</script>

