<template>
  <div class="progress-circle">
    <svg viewBox="0 0 108 108">
      <circle r="50" cx="54" cy="54"></circle>
      <circle r="50" cx="54" cy="54" :stroke-dashoffset="dashOffset"></circle>
    </svg>
    <slot></slot>
  </div>
</template>
<script>
export default {
  props: {
    percent: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    dashOffset() {
      return (1 - this.percent) * 314;
    },
  },
};
</script>
<style lang="scss" scoped>
  @import '~assets/style/variable.scss';
  .progress-circle {
    position: relative;
    svg {
      width: 100%;
      height: 100%;
      circle {
        fill: transparent;
        stroke-width: 8px;
      }
      circle:first-child {
        stroke: $color-theme-d;
      }
      circle:last-child {
        stroke: $color-theme;
        stroke-dasharray: 314;
        transform: rotate(-90deg);
        transform-origin: center;
      }
    }
  }
</style>
