import { createNamespacedHelpers } from 'vuex';

const {
  mapState,
} = createNamespacedHelpers('songModule');

export default {
  computed: {
    ...mapState([
      'playList',
      'miniPlayerHeight',
    ]),
  },
  methods: {
    handlePlayList() {
      throw new Error('must implement handlePlayList method');
    },
  },
  activated() {
    this.playListWatch = this.$watch('playList', this.handlePlayList, { immediate: true });
  },
  deactivated() {
    this.playListWatch();
  },
  mounted() {
    this.handlePlayList();
  },
};
