<template>
  <div
    class="progressWrapper"
    ref="progressWrapper"
    @click="progressClick($event)"
    @touchstart.prevent="progressDragStart($event)"
    @touchmove.prevent="progressDraging($event)"
    @touchend="progressDragEnd($event)"
  >
    <div class="progress" ref="progress"></div>
    <div class="progress-btn" ref="btn"></div>
  </div>
</template>
<script>
export default {
  props: {
    percent: {
      type: Number,
      default: 0,
    },
    progressWidth: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      touch: {},
      draging: false,
    };
  },
  methods: {
    setProgressPercent(translateX) {
      this.$refs.progress.style.width = `${translateX}px`;
      this.$refs.btn.style.transform = `translateX(${translateX}px)`;
    },
    progressClick(event) {
      if (event.target.closest('.progress-btn')) {
        return;
      }
      const x1 = this.$refs.progressWrapper.getBoundingClientRect().x;
      const x2 = event.pageX;
      const offset = x2 - x1;
      this.$emit('percent-change', offset / this.progressWidth);
    },
    progressDragStart(event) {
      const target = event.target.closest('.progress-btn');
      if (!target) {
        return;
      }
      this.draging = true;
      this.touch.startX = event.touches[0].pageX;
      this.touch.progressWidth = this.percent * this.progressWidth;
    },
    progressDraging(event) {
      if (!this.draging) {
        return;
      }
      let offset = event.touches[0].pageX - this.touch.startX;
      if (offset < 0) {
        offset = Math.max(0, this.touch.progressWidth + offset);
      } else {
        offset = Math.min(this.progressWidth, this.touch.progressWidth + offset);
      }
      this.setProgressPercent(offset);
    },
    progressDragEnd(event) {
      const target = event.target.closest('.progress-btn');
      if (!target) {
        return;
      }
      const nPercent = this.$refs.progress.offsetWidth / this.progressWidth;
      this.$emit('percent-change', nPercent);
      this.draging = false;
    },
  },
  watch: {
    percent() {
      if (!this.draging) {
        this.setProgressPercent(this.percent * this.progressWidth);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
  @import '~assets/style/variable';
  .progressWrapper {
    position: relative;
    background: rgba(0,0,0,.3);
    height: 4px;
    .progress {
      height: 100%;
      background: $color-theme;
      width: 0%;
    }
    .progress-btn {
      width: 16px;
      height: 16px;
      background: $color-theme;
      border-radius: 50%;
      border: 3px solid $color-text;
      position: absolute;
      left: -8px;
      top: -7px;
    }
  }
</style>
