<template>
  <div class="wrappers" ref="wrapper">
    <scroll
      ref="scroll"
      :click="true"
      class="recommend-wrapper"
    >
      <div class="recommend">
        <recommend-slider @img-loaded="refreshScroll"/>
        <div class="recommend-list">
          <h1>热门歌单推荐</h1>
        </div>
        <recommend-songs-list
          @songs-list-loaded="refreshScroll"
          @enter-songlist="enterSonglistHandle"
        />
      </div>
    </scroll>
    <transition name="songList">
      <router-view></router-view>
    </transition>
  </div>
</template>
<script>
import Scroll from 'components/common/scroll/Scroll.vue';
import setBottom from 'mixin/scaleDomHeight';
import RecommendSlider from './children/RecommendSlider.vue';
import RecommendSongsList from './children/RecommendSongsList.vue';

export default {
  mixins: [setBottom],
  components: {
    RecommendSlider,
    RecommendSongsList,
    Scroll,
  },
  methods: {
    refreshScroll() {
      setTimeout(this.$refs.scroll.refresh, 20);
    },
    handlePlayList() {
      if (this.playList.length === 0) {
        this.$refs.wrapper.style.bottom = '0px';
      } else {
        this.$refs.wrapper.style.bottom = `${this.miniPlayerHeight}px`;
      }
      this.$refs.scroll.refresh();
    },
    enterSonglistHandle({ id, name, imgSrc }) {
      this.$router.push({
        name: 'songListDetail',
        params: {
          id,
        },
        query: {
          name,
          imgSrc,
        },
      });
    },
  },
  data() {
    return {
      checkSlideLoad: false,
      fullScreenWatch: null,
    };
  },
};
</script>
<style lang="scss" scoped>
  @import '~assets/style/variable';
  .wrappers {
    position: fixed;
    top: 88px;
    width: 100%;
    bottom: 0px;
  }
  .recommend-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .recommend-list {
    > h1 {
      height: 65px;
      line-height: 65px;
      color: $color-theme;
      font-size: $font-size-medium;
      text-align: center;
    }
  }
  .songList-enter-active, .songList-leave-active {
    transition: all .3s;
  }
  .songList-enter, .songList-leave-to {
    transform: translateX(100%);
    opacity: 0;
  }
</style>
