<template>
  <div class="lyric-wrapper">
    <scroll
      ref="scroll"
      class="scroll"
      :data-arr="lyric"
    >
      <div class="lyric-lines" ref="lyric">
        <p
          v-for="(item, index) in lyric"
          :key="index"
          :class="{current: index === currentLineIndex}"
        >
          {{item.lyric}}
        </p>
      </div>
    </scroll>
  </div>
</template>
<script>
import Scroll from 'components/common/scroll/Scroll.vue';

export default {
  components: {
    Scroll,
  },
  props: {
    lyric: {
      type: Array,
      default() {
        return [];
      },
    },
    currentLineIndex: {
      type: Number,
      default: 0,
    },
  },
  watch: {
    currentLineIndex(nVal) {
      if (nVal >= 6) {
        const target = this.$refs.lyric.children[nVal - 6];
        this.$refs.scroll.scrollToElement(target, 800);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
  @import '~assets/style/variable.scss';

  .lyric-wrapper {
    width: 100%;
    height: 100%;
    .scroll {
      height: 100%;
      .lyric-lines {
        text-align: center;
        font-size: $font-size-medium;
        color: $color-text-l;
        line-height: 32px;
        p {
          &.current {
            color: $color-text;
          }
        }
      }
    }
  }
</style>
