<template>
  <base-slider
    v-if="sliderData.loaded"
    :sliderData="sliderData.data"
    @img-loaded="imgLoadHandle"
  />
</template>
<script>
import getBanner from 'api/recommend/banner';
import BaseSlider from 'components/common/slider/BaseSlider.vue';

export default {
  created() {
    this._getBannerData();
  },
  data() {
    return {
      sliderData: {
        loaded: false,
      },
      checkLoaded: false,
    };
  },
  methods: {
    _getBannerData() {
      getBanner().then((res) => {
        this.sliderData = {
          loaded: true,
          data: res,
        };
      });
    },
    imgLoadHandle() {
      if (!this.checkLoaded) {
        this.checkLoaded = !this.checkLoaded;
        this.$emit('img-loaded');
      }
    },
  },
  components: {
    BaseSlider,
  },
};
</script>
