import request from 'assets/js/network';

export default async function getBanner() {
  const res = await request({
    url: '/banner',
    params: {
      type: 1,
    },
  });
  if (res === 'err') {
    console.log('轮播图数据获取失败');
    return res;
  }
  return res.banners.map((elm) => ({
    imgSrc: elm.pic,
    href: elm.url,
  }));
}
